@mixin boton($color){
    background-color: $color;
    border: none;
    color: $negro;
    border-radius: 0.5rem;
    font-weight: $bold;
    text-decoration: none;
    padding: 0.5rem 1rem;
    text-align: center;
    font-size: 1.2rem;
    display: inline;
    width: auto;
    height: 3rem;
    
    &:hover{
        cursor: pointer;
        color: $negro !important;
    }
}

@mixin transicionBotones($colorIntenso){
    transition: all 0.6s ease;
    &:hover{
        background-color: $colorIntenso;
        // color: $blanco-gris;
    }
}

.sombra{
    -webkit-box-shadow: 2px 12px 26px 8px rgba(0,0,0,0.1);
    -moz-box-shadow: 2px 12px 26px 8px rgba(0,0,0,0.1);
    box-shadow: 2px 12px 26px 8px rgba(0,0,0,0.1);
}

.boton-rojo{
    @include boton($rojo);
    @include transicionBotones($rojo-intenso);
}

.boton-morado{
    background-color: $morado;
    border: none;
    color: $blanco;
    border-radius: 0.5rem;
    font-weight: $bold;
    text-decoration: none;
    text-align: center;
    font-size: 1.4rem;
    padding: 1rem 2rem;
    display: inline-block;
    width: auto;
    // height: 3rem;
    
    &:hover{
        cursor: pointer;
        background-color: $morado-intenso;
    }
}

.boton-amarillo{
    @include boton($amarillo);
    @include transicionBotones($amarillo-intenso);
}

.boton-verde{
    @include boton ($verde);
    @include transicionBotones($verde-intenso);
}

.boton-azul{
    @include boton ($azul);
    @include transicionBotones($azul-intenso);
}

.boton-volver{
    @include boton($amarillo);
    font-size: 2rem;
    height: auto;
    width: auto;
    color: $negro;
    @include transicionBotones($amarillo-intenso);
}

.boton-aumento{
    @include boton ($azul);
    color: $negro;
    &:focus{
        background-color: $azul-intenso;
    }
}

.boton-tipo{
    @include boton ($azul);
    color: $negro;
    &:focus{
        background-color: $azul-intenso;
    }
}


.boton-superior{
    background-color: $amarillo;
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include transicionBotones($amarillo-intenso);
    &:hover{
        transform: scale(1.15);
    }

}

.opcion-superior{
    margin: 2rem auto;
    display: flex;
    justify-content: flex-end;
    a{
        img{
            height: 60%;
            width: 60%;
            margin:auto;
        }
    }
}

.plantilla{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    margin-bottom: 2rem;
}

.tabla{
    border: 1px solid $gris;
    border-radius: 0.5rem;
    background-color: $blanco-gris;
    margin-top: 2rem;
    margin-bottom: 2rem;
    table{
        background-color: $blanco;
        width: 98%;
        margin: 2rem auto;
        th,td{
            border: 1px solid $gris;
            text-align: center;
            padding: 0.5rem 1rem;
            font-size: 1.2rem;
        }
    }

    @include tablet{
        table{
            th,td{
                font-size: 1.6rem;
                a{
                    color: $azul-intenso;
                }
            }
        }
    }
}

.comision{
    background-color: $verde !important;
}

.leyenda-representante{
    background-color: $azul;
}

.leyenda-vendedor{
    background-color: $verde;
}

.alerta{
    padding: .5rem;
    margin: 2rem auto;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    
    &.error{
        background-color: $rojo;
    }
    &.exito{
        background-color: $verdePrincipal;
    }
}

.fotos-actuales{
    display: grid !important;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;

    @include tablet{
        // grid-template-columns: repeat(3, 1fr);
    }
}