
.navegacion{
    margin-top: 1rem;
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition:  opacity .3s linear, height 0.5s linear;
    
    a{
        color: $blanco;
        font-size: 2.4rem;
        font-weight: $bold;
        text-align: center;
        width: auto;
    &:hover{
        transform: scale(1.1);
    }
        
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet{
        opacity: 1;
        visibility: visible;
        height: auto;
        flex-direction: row;
        a{
            font-size: 1.4rem;
        }
        display: grid;
        grid-template-columns: repeat(4,1fr);
    }
    // @include desktop{
    //     grid-template-columns: repeat(8,1fr);
    //     a{
    //         font-size: 1.4rem;
    //         margin-right: 2rem ;
    //         &:last-of-type {
    //         margin: 0;
    //         }
    //     }
    // }
}

/*Menu de hamburguesa*/
.mobile-menu{
    width: 5rem;
    margin: 1rem auto 0 auto;
    margin-top: 1rem;
    @include tablet{
        display: none;
    }
    img{
        width: 5rem;
        margin: auto;
        &:hover{
            cursor: pointer;
        }
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
    @include tablet{
        flex-direction: row;
    }
}

.header{
    background-color: $verdePrincipal;
    // padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include tablet{
        min-height: 10vh;
        height: auto;
    }
}

.logotipo{
    filter: invert(100%);
    display: block;
    width: 60%;
    margin: auto;
    text-align: center;
    img{
        width: 100%;
        height: auto;
        margin: auto;
    }
    @include tablet{
        width: 35%;
    }
}

.barra{
    .logotipo{
        width: 40%;
        min-width: 10rem;
        max-width: 20rem;
        padding: 1rem;
    }
}

.barra{
    @include tablet{
        display: grid;
        grid-template-columns: 30% 70%;
    }
}