//diseño para la pagina VENTA
.titulo-venta{
    margin-bottom: 4rem;
}
.resumen-venta{
    h4{
        text-align: center;
    }
}
.resumen-venta_informacion{
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    padding: 2rem;
    img{
        width: 90%;
        margin: auto;
    }
    div{
        width: 90%;
        margin: 1rem auto;
        // border: 1px solid black;
        padding: 2rem 4rem;
        ul{
            text-align: center;
            list-style: none;
            padding: 0;
        }
    }


    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        img{
            height: 20rem;
            width: auto;
        }
        div{
            width: auto;
        }
    }
}

.aviso-venta{
    border-radius: 2rem;
    padding: 2rem 1rem;
    text-align: center;
    font-weight: $bold;
    color: $blanco;
    width: 60%;
    margin: auto;
    background-color: red;

}

.form-venta{
    padding: 2rem 0;
}

form{
    p{
        color: $rojo;
    }
}