//DISEÑO DE LOS ANUNCIOS (PLANTILLAS) TANTO PARA SERVICIOS COMO PROPIEDADES
.imagen-servicio{
    width: 100%;
    margin: auto;
    img{
        border-radius: 1rem;
    }
}
.info-servicio{
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    .texto-servicio{
        margin: 2rem 4rem;
    }
    .boton{
        display: inline-block;
        margin: 0 auto;
    }
}

.anuncios{
    @include grid(2,2rem);
    margin: 4rem 2rem;
 
    .anuncio{
        
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: $blanco;
        // border: 1px solid black;
            img{
                width: 90%;
                height: auto;
                margin: 2rem auto;
                border-radius: 1rem;
                object-fit: cover;
            }
        .info-anuncio{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            h2{
                font-size: 1.4rem;
                margin: 0.5rem;
                color: $negro;
                font-weight: 500;
            }
            p{
                color: $verde;
                font-weight: $regular;
                margin: 0.5rem 0;
                font-size: 2rem;
            }
            &::before{
                content: '';
                display: block;
                height: 0.2rem;
                width: 80%;
                background-color: $morado;
                margin: auto;
            }
            
        }
        transition: all 550ms ease;
        &:hover{
            transform: scale(1.05);
        }
    }

    @include tablet{
        @include grid(4,2rem);
        .anuncio{

            .info-anuncio{
                h2{
                    font-size: 2rem;
                    margin: 1rem;
                    color: $negro;
                    font-weight: 500;
                }
                p{
                    color: $verde;
                    font-weight: $bold;
                    margin: 1rem 0;
                    font-size: 2rem;
                }
            }
        }
    }
}

#servicios{
    .anuncio{
        border-radius: 0;
        .info-anuncio{
            &::before{
                content: '';
                display: none;
                height: 0;
                width: 0;
                margin: 0;
            }
        }
    }
    display: flex;
    flex-direction: column;
    @include tablet{
        @include grid(3,2rem);
    }
}

.ubicacion{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    iframe{
        width: 90%;
        margin: auto;
    }
}
