.caracts-propiedad{
    padding-bottom: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .caracts-propiedad-listas{
        width: 90%;
        background-color: $blanco;
        border-radius: 1rem;
        ul{
            
            padding: 2rem;
            list-style: none;
            width: 100%;
            li{
                // border: 1px solid black;
                margin: 2rem auto;
                display: flex;
                img{
                    height: 6rem;
                    width: auto;

                }
                p{
                    margin: auto;
                    // padding: 0.5rem;
                    text-align: center;
                    font-size: 1.6rem;

                }
            }
        }
    }
    
    @include tablet{
        .caracts-propiedad-listas{
            ul{
                li{
                    p{
                        font-size: 1.8rem;
                    }
                }
            }
        }
    }
    @include desktop{
        .caracts-propiedad-listas{
            display: flex;
            justify-content: space-evenly;
        }
    }
}

.extra-propiedad{
    // background-color: blue;

    .extra-elementos{
        // background-color: green;
        ul{
           
            background-color: $blanco;
            border-radius: 1rem;
            padding: 2rem;
            width: 95%;
            margin: auto;
            list-style: none;
            // border: 1px solid black;
            
            justify-content: center;
            li{
                // border: 1px solid white;
                gap: 1rem;
                margin: 2rem;
                display: flex;
                img{
                    height: 6rem;
                    width: auto;
                }
                p{
                    margin: auto;
                    // padding: 0.5rem;
                    text-align: center;
                    font-size: 1.6rem;

                }
            }
        }
    }
    @include tablet{
        ul{
            display: grid;
            grid-template-columns: repeat(2,1fr);
        }
    }
}

.muebles-amenidades{
    // background-color: red;
    color: $blanco;
    margin-bottom: 4rem;
    padding: 4rem 0;
    div{
        border-radius: 1rem;
        background-color: green;
        width: 90%;
        margin: auto;
        h3{
            font-weight: $bold;
            text-align: start;
            margin: 1rem 4rem;
            &::after{
                content: '';
                background-color: $blanco;
                width: 100%;
                height: 2px;
                display: block;
                margin: auto;
                margin-top: 1rem;
            }
        }
        ul{
            list-style: none;
            padding: 0;
            li{
                width: 80%;
                margin: 1rem auto;
                padding-left: 2rem;
                display: grid;
                grid-template-columns: 30% 70%;
                align-items: center;
                img{
                    width: 50%;
                    filter: invert(1);
                }
                text-align: center;
                font-size: 2rem;
            }
        }
    }
    @include tablet{
        display: grid;
        grid-template-columns: 40% 40%;
        gap: 2rem;
        justify-content: center;
        div{
            width: 100%;
            margin: 0;
        }
    }
}

.opciones-compra{
    gap: 2rem;
    h3{
        margin-top: 0;
    }
    div{
        // border: 1px solid black;
        ul{
            padding:0;
            list-style: none;
            li{
                border: 1px solid white;
                width: 80%;
                margin: 1rem auto;
                display: flex;
                img{
                    height: 5rem;
                    width: auto;
                }
                p{
                    margin: auto;
                    // padding: 0.5rem;
                    text-align: center;
                    font-size: 1.6rem;

                }
            }
        }
    }
    img{
        height: 100%;
        object-fit: cover;
    }
    @include tablet{
        display: grid;
        grid-template-columns: 55% 40%;
        justify-content: center;
    }
}

.enlace-google{
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    a{
        margin: 0;
    }
    @include tablet{
        a{
            width: 40%;
        }
    }
}