//listado de las propiedades en el index del administrador
.inmuebles{
    @include grid(2,2rem);

    .inmueble{
        padding: 0.1rem;
        border: 2px solid $blanco;
        border-radius: 1rem;
        height: auto;
        img{
            // border: 1px solid black;
            width: 100%;
            height: auto;
            border-radius: 1rem;
            margin-bottom: 1rem;
        }
        transition: all 300ms ease;
            &:hover{
                cursor: pointer;
                transform: scale(1.05);
            }
        .info-inmueble{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            // height: 26rem;
            
            .direccion{
                color: $negro;
                font-weight: 600;
                padding: 0.8rem;
                margin: 0 auto;
                text-align: center;
                font-size: 1.4rem;
                height: 11rem;
            }
            .info-inferior{
                .precio,.pago,.estado{
                    margin: 0 auto;
                    text-align: center;
                    font-size: 1.4rem;
                    font-weight: $bold;
                }
                .estado{
                    color: red;
                }
            }
        }
    }
    @include desktop{
        @include grid(4,2rem);
    }
}

.buscador{
    margin: 1rem auto;
}

.beneficios{
    margin:1rem 0;
    @include grid(2,1rem);
    // display: flex;
    // justify-content: space-between;
    width: 100%;
    .beneficio{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
        // border-right: 1px solid $gris;
        &:last-of-type{
            border-right: none;
        }
        img{
            height: 35%;
            width: 35%;
            margin: auto;
            border-radius: 0;
        }
        p{
            font-size: 1.2rem;
            margin: auto 0.8rem; 
        }
    }
}

.precio{
    color: $verdePrincipal;
}

.opciones{
    margin-top: 1.4rem;
    
    @include grid(2,0.5rem);
    a{
        font-size: 1rem;
        width: 100%;
        height: auto;
        margin: auto;
        color: $negro;
        &:hover{
            color: $blanco;
        }
    }
    form{
        height: 100%;
        input{
            height: 100%;
            width: 100%;
            font-size: 1rem;
        }
    }
    @include tablet{
        a{
            font-size: 1.5rem;
        }
        form{
            input{
                font-size: 1.5rem;
            }
        }
    }
}

.opciones-compra{
    margin-bottom: 4rem;
}

.aviso-opciones{
    font-size: 1rem;
}